.App {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.Card{
  background-color: #61dafb65;
  min-width: 15rem;
  min-height: 16rem;
  border-radius: 1rem ;
  
}

.Cardother{
  background-color: #61dafb65;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  min-width: 20rem;
  min-height: 1rem;
  border-radius: 1rem ;
  margin: 1rem;
  
}

.containerGeneral{
  position: relative;
  width: 100%;
  display: grid;
  justify-content: center;
  align-items: center;
}

.button{
  Background-color: #61dafbb2;;
  color: #000;
  min-width: 6rem;
  min-height: 2rem;
  border-radius: 0.5rem ;
  margin-top: 1rem;
 }

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
